import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NavController } from '@ionic/angular';
import { GetDataBackendService } from '../../service/get-data-backend.service';
import { LocalStorageServiceService } from '../../service/local-storage-service.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  SinCorreo: Boolean = false;
  ErrorClave: Boolean = false;
  ErrorRespuestas: Boolean = false;
  PassSave: Boolean = false;
  ErrorNewPass: Boolean = false;
  ShowPass: Boolean = false;
  ShowLogin: Boolean = true;
  ModalEmail: Boolean = false;
  ShowPreguntas: Boolean = false;
  ModalPass: Boolean = false;

  LoginForm: FormGroup;
  RecuperarPass: FormGroup;
  PreguntasForm: FormGroup;
  PassForm: FormGroup;

  PregSeguridad: any = []
  DataUser: any
  MsgError: any

  constructor(
    public navCtrl: NavController,
    public _localstorage: LocalStorageServiceService,
    public _ConsultasB: GetDataBackendService,
    public formBuilder: FormBuilder
  ) {

    this.LoginForm = this.formBuilder.group({
      Email: ['', [Validators.required, Validators.email, Validators.minLength(2)]],
      Password: ['', [Validators.required, Validators.minLength(6)]]
    })

    this.RecuperarPass = this.formBuilder.group({
      Email: ['', [Validators.required, Validators.email]]
    })

    this.PreguntasForm = this.formBuilder.group({
      RespuestaUno: ['', [Validators.required, Validators.minLength(3)]],
      RespuestaDos: ['', [Validators.required, Validators.minLength(3)]]
    })

    this.PassForm = this.formBuilder.group({
      NewPassword: ['', [Validators.required, Validators.minLength(6)]]
    })

  }

  ngOnInit() {
  }

  get DatosLogin() { return this.LoginForm.value }
  get EmailRecuperacion() { return this.RecuperarPass.value }
  get Respuestas() { return this.PreguntasForm.value }

  //busco los datos del usuario que esta iniciando seccion 
  GuardarDatosLogin() {

    this._ConsultasB.GetUserEmail(this.DatosLogin.Email).then((ResUser: any) => {

      if (ResUser.length > 0) {
        // si tengo datos eso quiere decir que ese correo si existe 

        if (ResUser[0].Password === this.DatosLogin.Password) {
          // verifico si la clave ingresada es igual a la que esta en la base de datos y lo mando a home 

          this._localstorage.GuardarItem('DatosUser', JSON.stringify(ResUser[0]))
          this.ClearForm()
          this.navCtrl.navigateForward("/home");

        } else {
          //si las claves son diferentes la clave es incorrecta 
          this.ErrorClave = true
        }

      } else {
        // sino existe datos el correo es incorrecto 
        this.SinCorreo = true
      }

      this.HideAlert()

    }).catch(error => console.error(error))
  }

  OptionPass() {
    //Mostrar u ocultar la clave
    switch (this.ShowPass) {
      case true:
        this.ShowPass = false
        break;

      case false:
        this.ShowPass = true
        break;
    }
  }

  HideAlert() {
    //escondo la alerta mostrada despues de 2 segundos
    setTimeout(() => {
      this.SinCorreo = false
      this.ErrorClave = false
      this.ErrorRespuestas = false
      this.PassSave = false
      this.ErrorClave = false
    }, 2000);
  }

  GetData() {
    try {
      //Obtengo los datos para el cambio de clave 

      if (this.EmailRecuperacion) {
        this._ConsultasB.GetUserEmail(this.EmailRecuperacion.Email).then((Res: any) => {

          if (Res.length > 0) {

            this.DataUser = Res[0]

            this.PregSeguridad = {
              "PreguntaDos": Res[0].PreguntaDos,
              "PreguntaUno": Res[0].PreguntaUno,
              "RespuestaDos": Res[0].RespuestaDos,
              "RespuestaUno": Res[0].RespuestaUno,
            }

            this.ModalEmail = false
            this.ShowPreguntas = true

          } else {
            this.SinCorreo = true
            this.HideAlert()
          }

        })
      }

    } catch (error) {
      console.error(error)
    }
  }

  VerifyPreguntas() {
    try {
      //Verifico las respuestas de seguridad que coloco el usuario 
      if (this.Respuestas.RespuestaUno === this.PregSeguridad.RespuestaUno && this.Respuestas.RespuestaDos === this.PregSeguridad.RespuestaDos) {
        this.ShowPreguntas = false
        this.ModalPass = true
      } else {
        this.ErrorRespuestas = true
        this.HideAlert()
      }

    } catch (error) {
      console.error(error)
    }
  }

  SavePassword() {
    try {
      //Guardo la nueva clave del usuario 
      const Pass = this.PassForm.value

      if (this.DataUser.Password != Pass.NewPassword) {
        this.DataUser.Password = Pass.NewPassword

        this._ConsultasB.UpdateUser(this.DataUser).then((ResUpdate: any) => {

          if (ResUpdate.status) {
            this.PassSave = true
            this.HideAlert()

            setTimeout(() => {
              this.ClearForm()
              this.ModalPass = false
              this.ShowLogin = true
            }, 1000);

          } else {

            this.MsgError = 'No se pudo guardar la contraseña'
            this.ErrorClave = true
            this.HideAlert()

          }

        }).catch(error => console.error(error))

      } else {
        this.MsgError = 'La Contraseña debe ser distinta a la anterior'
        this.ErrorClave = true
        this.HideAlert()
      }

    } catch (error) {
      console.error(error
      )
    }
  }

  ClearForm() {
    //Limpio los formularios 
    this.LoginForm.patchValue({
      Email: '',
      Password: ''
    });

    this.RecuperarPass.patchValue({
      Email: ''
    })

    this.PreguntasForm.patchValue({
      RespuestaUno: '',
      RespuestaDos: ''
    })

    this.PassForm.patchValue({
      NewPassword: ''
    })

  }

}
